/* Misc */
hr {
  border: 0;
  height: 1px;
  background-color: var(--color-border);
  margin: var(--gap) 0;
}

abbr {
  -webkit-font-variant: small-caps;
  -moz-font-variant: small-caps;
  -ms-font-variant: small-caps;
  font-variant: small-caps;
  font-weight: var(--font-weight-700);
  text-transform: lowercase;
  color: gray;
}

abbr[title]:hover {
  cursor: help;
}

input,
abbr,
acronym,
blockquote,
code,
kbd,
q,
samp,
var {
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}